.navbar{
    display: flex;
    justify-content: space-between;
    background-color: beige;
    width: 100%;
}
.logo img{
    height: 80px;
    width: 80px;
    margin-left: 70px;
}
.list ul{
    display: flex;
    margin-right: 70px;
    list-style: none;
}
.list ul li a{
    display: flex;
    padding: 10px;
    list-style: none;
    outline: none;
    text-decoration: none;
}
@media screen and (min-width: 400px) and(max-width: 1180px){
    .navbar{
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    
}