.container{
    display: flex;
    justify-content: space-around;
    max-width: 60%;
    height: 60vh;
    margin: 0 auto;
    background-color: rgb(208, 216, 218);
    margin-top: 20px;
    
}

.container1 button{
   
    background-color: black;
    color: white;
    padding: 10px 50px;
    margin: 10px;
    margin-top: 20px;
    font-size: 1rem;
    gap: 8px;
    border-radius: 10px;
   
}
span{
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

 button.btn1{

padding: 10px 170px;
background-color: aliceblue;
color: black;
font-size: 1rem;
}
.form-control input{
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
   
}
input{
   padding: 10px;
   border-radius: 10px;
   margin: 20px;
   border:none;
   
}
label{
    position: absolute;
    z-index: 1;
    margin-top: -12px;
    margin-left: 43px;
    font-size: 1rem;
    
}
.text{
    width: 100%;
    padding: 40px;
    border: none;
    margin-left: 22px;
    border-radius: 10px;
   
}


.container2 img{
 width: 50vh;
 height: 50vh;
 
}
 #btn-2{
    margin-left: 400px;
    padding: 10px 60px;
}
@media screen and (min-width: 200px) and(max-width: 1180px){
    .container{
       
        display: flex;
        flex-direction: column;
    }
    
}